.link{
    width: 229px;
    max-width: 100%;
    background-color: var(--golden-color);
    display: block;
    border-radius: 20px;
    font-family: var(--futuraBold-font);
    font-size: 13px;
    text-decoration: none;
    padding: 17px 46px;
    height: 54px;
    text-align: center;
    &:hover{
        text-decoration: none;
    }
}
.click{
    max-width: 162px;
    width: 100%;
    height: 51px;
    border: 2px solid black;
    background: transparent;
    text-decoration: none;
    text-transform: uppercase;
    color: var(--black-color);
    font-size: 13px;
    font-family: var(--futuraBold-font);
    text-align: center;
    &:hover{
        text-decoration: none;
        color: black;
    }
}
